.App {
  font-family: Arial, sans-serif;
  text-align: center;
  padding: 20px;
}

.question-box {
  margin-bottom: 20px;
}

.question-box input {
  padding: 10px;
  width: 300px;
  margin-right: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.answer-box {
  margin-top: 20px;
}

.answer-box p {
  font-size: 16px;
  color: #333;
}

button {
  padding: 10px 15px;
  border: none;
  background-color: #007bff;
  color: white;
  cursor: pointer;
  border-radius: 4px;
}

button:disabled {
  background-color: #cccccc;
}

/*以下為排行榜設計 ------------------------------------*/

.leaderboard {
  padding: 20px;
  font-family: Arial, sans-serif;
}

.leaderboard h1 {
  text-align: center;
  margin-bottom: 20px;
}

.leaderboard-table {
  width: 80%;
  margin: 0 auto;
  border-collapse: collapse;
}

.leaderboard-table th,
.leaderboard-table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: center;
}

.leaderboard-table th {
  background-color: #f4f4f4;
  font-weight: bold;
}

.leaderboard-table tr:nth-child(even) {
  background-color: #f9f9f9;
}

.leaderboard-table tr:hover {
  background-color: #f1f1f1;
}
